<template>
  <div id="gaa">
    <wu />
    <dibu />
    <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
      <div>
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="playerOptions"
        ></video-player>

        <el-carousel indicator-position="outside" id="lun3">
          <el-carousel-item id="lun2">
            <div v-for="(item,k) in list" id="lun1" :key="k" @click="fbA(item.naem)">
              <img :src="item.img1" />
              <p>{{item.naem}}</p>
            </div>
          </el-carousel-item>
          <el-carousel-item id="lun2">
            <div v-for="(item,k) in list1" :key="k" id="lun1" @click="fbA(item.naem)">
              <img :src="item.img1" />
              <p>{{item.naem}}</p>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div>
        <p>附近商家</p>
        <div v-for="(item,k) in dataList" class="gaa-1" :key="k" @click="faA(item)">
          <img :src="'http://elm.cangdu.org//img/'+item.image_path" />
          <div>
            <p style="font-weight: 600;">{{item.name}}</p>
            <van-rate
              :value="item.rating"
              allow-half
              void-icon="star"
              void-color="#eee"
              style="color:#FEBE40;font-size:0.1rem"
            />
            {{item.rating}}
            <span style="color:#000">月售{{item.rating_count}}单</span>

            <p>
              ￥{{item.float_minimum_order_amount}}起送
              <span>{{item.float_delivery_fee}}</span>
            </p>
          </div>
          <div>
            <p>
              <span v-for="(val,k) in item.supports" :key="k" style="float:right">{{val.icon_name}}</span>
            </p>
            <p>
              <span
                style="margin-left: 0.6rem;border:1px solid;background:#3792E3;color:#fff"
              >{{item.delivery_mode.text}}</span>
              <span
                style="float:right;color:#3792E3;border:1px solid #3792E3"
              >{{item.supports[1].name}}</span>
            </p>
            <p>
              <span style="float:right">
                {{item.distance}}/
                <span style="color:#3792E3">{{item.order_lead_time}}</span>
              </span>
            </p>
          </div>
        </div>
      </div>
    </mescroll-vue>
  </div>
</template>

<script>
import wu from "./toubiao";
import dibu from "./dibu";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
  components: {
    wu,
    MescrollVue,
    dibu
  },
  name: "gaa",
  data() {
    return {
      list: [],
      list1: [],
      q: 8,
      w: 0,
      busy: true,
      txt: "el-icon-star-off",
      txt1: "主页面",

      mescroll: null, // mescroll实例对象
      mescrollDown: {}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.upCallback, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
        //以下是一些常用的配置,当然不写也可以的.
        page: {
          num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
          size: 5 //每页数据条数,默认10
        },
        htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
        noMoreSize: 5 //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
        // 避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
        // 这就是为什么无更多数据有时候不显示的原因
      },
      dataList: [], // 列表数据

      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "video/ogg",
            type: "video/webm",
            type: "video/mp4",
            // mp4
            src: "http://vjs.zencdn.net/v/oceans.mp4"
          }
        ],
        poster: "/static/rBACE1KwRmCS2DRgAAFYIFXH6bc789.jpg", //你的封面地址
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //全屏按钮
        }
      }
    };
  },
  mounted() {
    this.axios(
      `https://www.easy-mock.com/mock/5d47d0537f5a7d14b6b7fb51/example_copy/qune`
    ).then(e => {
      this.list = e.data.qune1.slice(0, 8);
      this.list1 = e.data.qune1.slice(1, 9);
    });
    this.axios(
      `http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`
    ).then(e => {
      this.dataList = e.data.splice(this.w, this.q);
    });
  },
  beforeMount() {
    localStorage.biao = this.txt;
    localStorage.zhu = this.txt1;
  },

  methods: {
    // mescroll组件初始化的回调,可获取到mescroll对象
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    // 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback(page, mescroll) {
      // 联网请求
      this.axios
        .get(
          "http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=",
          {
            params: {
              num: page.num, // 页码
              size: page.size // 每页长度
            }
          }
        )
        .then(response => {
          // 请求的列表数据
          this.q += 8;
          this.w += 8;
          let arr = response.data.splice(this.q, this.w);

          // 如果是第一页需手动置空列表
          if (page.num === 1) this.dataList = [];
          // 把请求到的数据添加到列表
          this.dataList = this.dataList.concat(arr);
          // console.log(this.dataList.concat)
          // 数据渲染成功后,隐藏下拉刷新的状态
          this.$nextTick(() => {
            mescroll.endSuccess(arr.length);
          });
        })
        .catch(e => {
          // 联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          mescroll.endErr();
        });
    },
    faA(id) {
      this.$router.push({ name: "xiang", query: { id: id } });
    },
    fbA(e) {
      localStorage.zhu = e;
      this.$router.push("/luntiao");
    }
  }
};
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mescroll {
  position: fixed;
  top: 2.5rem;
  bottom: 2rem;
  height: auto;
}
.gaa-1 {
  width: 100%;
  height: 3rem;
  border-bottom: 1px solid #999;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
.gaa-1 > img {
  width: 2rem;
  height: 2rem;
}
.gaa-1 > div {
  width: 4rem;
  height: 100%;
  margin-left: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  padding-top: 0.3rem;
  box-sizing: border-box;
}
.gaa-1 > div > p {
  width: 100%;
  height: 0.6rem;
  box-sizing: border-box;
  line-height: 0.6rem;
  overflow: auto;
}

#lun1 {
  width: 25%;
  height: 50%;
  text-align: center;
}
p {
  margin: 0;
  padding: 0;
}
#lun2 {
  width: 100%;
  height: 3.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
#lun1 > img {
  width: 1rem;
  height: 1rem;
  margin: 0 0.4rem;
}
#lun3 {
  height: 4rem;
}
</style>



